import Link from "next/link"
import { UserCircle } from 'lucide-react'
import React from "react"

export default function Navbar() {
  return (
    <nav className="bg-white shadow-sm">
      <div className="container mx-auto px-4 sm:px-5">
        <div className="flex justify-between items-center h-16">
          <div className="flex space-x-8">
            <Link href="/" className="font-medium text-gray-900">
              Dashboard
            </Link>
            <Link href="/buy" className="font-medium text-gray-600 hover:text-gray-900">
              Buy Shares
            </Link>
            <Link href="/sell" className="font-medium text-gray-600 hover:text-gray-900">
              Sell Shares
            </Link>
            <Link href="/referrals" className="font-medium text-gray-600 hover:text-gray-900">
              Referrals
            </Link>
            <Link href="/account" className="font-medium text-gray-600 hover:text-gray-900">
              Account
            </Link>
          </div>
          <div className="flex items-center space-x-4">
            <Link href="/register" className="font-medium text-gray-600 hover:text-gray-900">
              Register
            </Link>
            <Link href="/login" className="font-medium text-gray-600 hover:text-gray-900">
              Login
            </Link>
            <Link href="/profile" className="text-blue-600">
              <UserCircle className="h-8 w-8" />
            </Link>
          </div>
        </div>
      </div>
    </nav>
  )
}
